import API from "./cfg.service";

function handleError(e) {
  const code = (e && e.response && e.response.status) ? e.response.status : null;

  if(code === 401){
    console.log("Acceso no autorizado.");
    document.cookie = "jwt=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
    localStorage.removeItem("token");
    localStorage.removeItem("user");
  }

  if(code === 422){
    console.log("Error de validación");
    return e.response
  }

  return false;
}

/**
 * Inicia y Establece una sesion en localStorage & Cookie.
 */

const login = async (payload) => {
  return await API
    .post("auth/", payload)
    .then((response) => {
      if (response.data.token) {
        let user = response.data.user
            user.roles = response.data.roles
        localStorage.setItem("user", JSON.stringify(user));
        localStorage.setItem("token", JSON.stringify(response.data.token));
      }
      return response.data;
    }).catch((r) => {
      return handleError(r);
    });
};

/**
 * Finaliza la sesion actual.
 */

const logout = async () => {
  localStorage.removeItem("user");
  localStorage.removeItem("token");

  return API.get("/auth/logout").then((response) => {
    return response.data;
  });
};

/**
 * Recupera los datos almacenados en storage, del usuario actualmente logueado
 */

const getCurrentUser = () => {
  const user = JSON.parse(localStorage.getItem("user"))
  if(user){
    user.accessToken = JSON.parse(localStorage.getItem("token"))
    return user;
  }
  return null;
};

const AuthService = {
  login,
  logout,
  getCurrentUser,
}

export default AuthService;