import React from "react"
import { Routes, Route } from "react-router-dom"

// MUI
import { createTheme, ThemeProvider } from '@mui/material/styles'
import CssBaseline from '@mui/material/CssBaseline'
import Box from '@mui/material/Box'
import Toolbar from '@mui/material/Toolbar'
import Container from '@mui/material/Container'

// Estilos Globales para Toastify
import 'react-toastify/dist/ReactToastify.css'

// Layout
import MenuAppBar from 'components/layout/navigation/Navbar'
import Footer from 'components/layout/navigation/Footer'

import ProtectedRoute from 'router/middleware'
import router from 'router/index.js'

const App = () => {
  
  const mdTheme = createTheme();
  return (
    <ThemeProvider theme={mdTheme}>
      <Box sx={{ display: 'flex' }}>
        <CssBaseline />
        <MenuAppBar />
        <Box component="main"
          sx={{
            flexGrow: 1,
            height: '100vh',
          }}
        >
          <Toolbar />
          <Box>
            <Container component="main" maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
              <Routes>
                {router.map((route) => (
                  <Route 
                    key={route.path} 
                    path={route.path} 
                    element={
                      (route.protected) 
                      ? <ProtectedRoute rolesReq={route.rolesReq}> {route.component} </ProtectedRoute>
                      : route.component
                    }
                  />
                ))}
              </Routes>
            </Container>
          </Box>
        </Box>
        <Footer />
      </Box>
    </ThemeProvider>
  );
};

export default App;
