import API from "./cfg.service";

function handleError(error) {
  const code = (error && error.response && error.response.status) ? error.response.status : null;

  if(code === 401){
    console.log("Acceso no autorizado.");
    document.cookie = "jwt=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
    localStorage.removeItem("token");
    localStorage.removeItem("user");
  }
}

/**
 * Modificar los datos personales del usuario.
 */

const update = async (id, payload) => {
  return API.put("users/" + id, payload)
  .then((response) => {
    return response;
  }).catch((r) => {
    return handleError(r);
  });
};

/**
 * Recupera los datos actualizados del usuario actualmente logueado. (UTILIZANDO LA COOKIE)
 */

 const getUpdatedUser = async () => {
  return await API
    .get("/auth/me")
    .then((response) => {
      let user = response.data.currentUser
          user.accessToken = JSON.parse(localStorage.getItem("token"))
      return user;
    }).catch((r) => {
      return handleError(r);
    })
};

/**
 * Modificar la clave actual del usuario.
 */

 const updatePassword = async (id, payload) => {
  return API.put("auth/password/" + id, payload)
  .then((response) => {
    return response
  }).catch((r) => {
    return handleError(r);
  });
}

/**
 * Obtener la actividad del usuario actualmente logueado.
 */

const getLogActivity = async () => {
  return API.get("/logs");
};


const UserService = {
    update,
    updatePassword,
    getUpdatedUser,
    getLogActivity
};

export default UserService;
