import React, { useState, useEffect, useCallback } from "react";
// Componentes Material.
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
// Toast
import { ToastContainer, toast } from 'react-toastify';
// DataService. 
import UserService from "services/user.service"
import CustomerService from "services/customer.service"

const Profile = () => {

    const [isLoading, setIsLoading] = useState(false);
    // DB Local.
    const [id, setId]                       = useState("");
    const [lastName, setLastName]           = useState("");
    const [firstName, setfirstName]         = useState("");
    const [emailAddress, setEmailAddress]   = useState("");
    const [customerId, setCustomerId]       = useState("");
    // SAP
    const [provincia, setProvincia]         = useState("");
    const [localidad, setLocalidad]         = useState("");
    const [direccion, setDireccion]         = useState("");
    const [jefeVenta, setJefeVenta]         = useState("");
    const [vendedor, setVendedor]           = useState("");
    const [telefono, setTelefono]           = useState("");

    const getUser = useCallback( () => {
        CustomerService.GetByKUNNR().then((result) => {
            if(result && result.cliente){
                const cliente = result.cliente[0]
                setVendedor(cliente.VendedorTxt);
                setProvincia(cliente.ProvinciaTxt);
                setLocalidad(cliente.CiudadTxt);
                setDireccion(cliente.Direccion);
                setJefeVenta(cliente.JefeVentasTxt);
                setTelefono(cliente.Telefono);
            }
        });

        UserService.getUpdatedUser().then((user) => {
            if(user){
                setId(user.id)
                setLastName(user.lastName)
                setfirstName(user.firstName)
                setEmailAddress(user.email)
                setCustomerId((user.customerId) ? user.customerId : '')
            }
        });
    }, [])

    useEffect(() => {
        getUser();
    }, [getUser]);

    const onChangeLastName = (e) => { 
        const lastName = e.target.value 
        setLastName(lastName)
    }

    const onChangeFirstName = (e) => {
        const firstName = e.target.value
        setfirstName(firstName)
    }

    const onChangeEmailAddress = (e) => {
        const emailAddress = e.target.value
        setEmailAddress(emailAddress)
    }

    const handleUpdate = (event) => {
        event.preventDefault();

        if(!lastName) return toast.error("El Apellido no puede estar vacío.")
        if(!firstName) return toast.error("El Nombre no puede estar vacío.")
        if(!emailAddress) return toast.error("El Email no puede estar vacío.")

        const formData = {
            lastName: lastName,
            firstName: firstName,
            email: emailAddress,
        }

        setIsLoading(true);

        UserService.update(id, formData).then(
            (response) => {
                toast.success(response.data.message)
                setIsLoading(false);

                var user = JSON.parse(localStorage.getItem("user"))

                if(user){
                    user.lastName = lastName
                    user.firstName = firstName
                    user.email = emailAddress
                    
                    localStorage.setItem("user", JSON.stringify(user));
                }
            },
            (error) => {
              const resMessage =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
              toast.error(resMessage)
              setIsLoading(false);
            }
        );
    }

    return (
        <React.Fragment>

            <ToastContainer
                position="bottom-left"
                autoClose={3000}
                hideProgressBar
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
            />

            <Grid container spacing={3}>
                <Grid item xs={12}>
                    <Typography variant="h7" display="block" gutterBottom sx={{py:3}}>
                        DATOS COMERCIALES 
                    </Typography>

                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                            <TextField 
                                fullWidth 
                                label="Vendedor" 
                                disabled
                                size="small"
                                value={vendedor}
                                variant="outlined" />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField 
                                fullWidth 
                                label="Jefe Ventas" 
                                disabled
                                size="small"
                                value={jefeVenta}
                                variant="outlined" />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                                <TextField 
                                    id="customerId"
                                    name="customerId"
                                    fullWidth 
                                    label="Nº Cliente" 
                                    disabled
                                    size="small"
                                    value={customerId}
                                    variant="outlined" />
                            </Grid>
                    </Grid>
                </Grid>

                <Grid item xs={12}>
                    <Typography variant="h7" display="block" gutterBottom sx={{py:3}}>
                        DATOS PERSONALES 
                    </Typography>

                    <form onSubmit={handleUpdate}>
                        <Grid container spacing={3}>
                            <Grid item xs={12} sm={6}>
                                <TextField 
                                    id="lastName"
                                    name="lastName"
                                    fullWidth
                                    label="Apellido" 
                                    size="small"
                                    value={lastName}
                                    onChange={onChangeLastName}
                                    variant="outlined" />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField 
                                    id="firstName"
                                    name="firstName"
                                    fullWidth 
                                    label="Nombre" 
                                    size="small"
                                    value={firstName}
                                    onChange={onChangeFirstName}
                                    variant="outlined" />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField 
                                    id="emailAddress"
                                    name="emailAddress"
                                    fullWidth 
                                    label="Email" 
                                    size="small"
                                    value={emailAddress}
                                    onChange={onChangeEmailAddress}
                                    variant="outlined" />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <TextField
                                    fullWidth
                                    label="Telefono" 
                                    disabled
                                    size="small"
                                    value={telefono}
                                    variant="outlined" />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    fullWidth
                                    label="Provincia" 
                                    disabled
                                    size="small"
                                    value={provincia}
                                    variant="outlined" />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField 
                                    fullWidth 
                                    label="Localidad" 
                                    disabled
                                    size="small"
                                    value={localidad}
                                    variant="outlined" />
                            </Grid>
                            <Grid item xs={12} sm={12}>
                                <TextField 
                                    fullWidth 
                                    label="Dirección" 
                                    disabled
                                    size="small"
                                    value={direccion}
                                    variant="outlined" />
                            </Grid>

                            <Grid item xs={12} sx={{ mt:1, textAlign: {sm: 'left', xs: 'center'}}}>
                                <Button 
                                    variant="contained"
                                    size="small"
                                    type="submit"
                                    sx={{ m:1, 'width': '160px' }}
                                    disabled={isLoading}
                                >Guardar Cambios</Button>
                                <Button 
                                    variant="contained"
                                    color="secondary"
                                    size="small"
                                    sx={{ m:1, 'width': '160px' }}
                                    onClick={getUser}
                                >Reiniciar</Button>
                            </Grid>
                        </Grid>
                    </form>
                </Grid>
            </Grid>
        </React.Fragment>
    );
}

export default Profile;