import axios from 'axios';
// http://staging.crm.baggio.com.ar/api
// http://dev.crm.baggio.com.ar/api/

const api_dev = 'http://localhost:8080/api'
const api_prd = window.location.origin + '/api'

console.info('ENV: ', (process.env.NODE_ENV))
console.info('API_URL: ', (process.env.NODE_ENV === 'development') ? api_dev : api_prd )

export default axios.create({
    baseURL: (process.env.NODE_ENV === 'development') 
      ? api_dev
      : api_prd,
    methods: ['GET', 'POST', 'PUT', 'DELETE', 'OPTIONS'],
    withCredentials: true,
    headers: {
      //  'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json',
    }
});