
import Error404 from "components/errors/404";

import Home from "components/Home";
import Account from "components/Account/Account";

import Login from "components/Auth/Login";
import Signup from "components/Auth/Signup";

import BoardUser from "components/Users/Index";
import BoardModerator from "components/Moderator/Index";
import BoardAdmin from "components/Administrador/Index";

// Errores
import Error401 from 'components/errors/401'

const router = [
  {
    path: '/401',
    name: 'Error 401',
    component: <Error401 />,
    protected: false,
  },
  {
    path: '*',
    name: 'Error 404',
    component: <Error404 />,
    protected: false,
  },
  {
    path: '/',
    name: 'Inicio',
    component: <Home />,
    protected: false,
  },
  {
    path: '/login',
    name: 'Acceder',
    component: <Login />,
    protected: false,
  },
  {
    path: '/signup',
    name: 'Registrarse',
    component: <Signup />,
    protected: false,
  },

  {
    path: '/profile',
    name: 'Perfil',
    component: <Account />,
    protected: true,
    rolesReq: ["ROLE_USER", "ROLE_MODERATOR", "ROLE_ADMIN"]
  },
  {
    path: '/user',
    name: 'Usuario',
    component: <BoardUser />,
    protected: true,
    rolesReq: ["ROLE_USER"]
  },
  {
    path: '/mod',
    name: 'Moderador',
    component: <BoardModerator />,
    protected: true,
    rolesReq: ["ROLE_MODERATOR"]
  },
  {
    path: '/admin',
    name: 'Administrador',
    component: <BoardAdmin />,
    protected: true,
    rolesReq: ["ROLE_ADMIN"]
  },
];

export default router;