import API from "./cfg.service";

const getPublicContent = () => {
  return API.get("/users/public");
};

const getUserBoard = () => {
  return API.get("/users/user");
};

const getModeratorBoard = () => {
  return API.get("/users/mod");
};

const getAdminBoard = () => {
  return API.get("/users/admin");
};

const UserService = {
  getPublicContent,
  getUserBoard,
  getModeratorBoard,
  getAdminBoard,
};

export default UserService;
