const ProtectedRoute = ({ rolesReq, children }) => {
  const { pathname } = useLocation();
  const user = AuthService.getCurrentUser();

  let navigate = useNavigate();

  // Inicialmente valido el rol del usuario.
  //let allow = (user && user.roles.includes(rolesReq)) ? true : false
  let allow =  (user && user.roles) ? _.intersection(user.roles, rolesReq) : []
  
  useEffect(() => { 
    // Si el usuario no se encuentra logueado.
    // Se debe redirigir al login, siempre y cuando no se encuentre ya en esa vista.
    if (!user && pathname !== '/login') {
      navigate("/login");
    }

    // Si el usuario se encuentra logueado.
    // Se debe validar el rol requerido para la seccion a la cual intenta acceder.
    if(user && user.roles && pathname !== '/user' && !allow.length){
      navigate("/401");
    }
  })

  return children;
};

export default ProtectedRoute;