import React, { useState, useEffect } from "react";
import { Link, useNavigate } from 'react-router-dom';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';

import { ToastContainer, toast } from 'react-toastify';

import AuthService from "services/auth.service";

const Login = () => {
  let navigate = useNavigate();

  useEffect(() => {
    const currentUser = AuthService.getCurrentUser();

    if(currentUser && currentUser.accessToken){
      navigate("/user");
    }

  });

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);

  const onChangeUsername = (e) => {
    const username = e.target.value;
    setUsername(username);
  };

  const onChangePassword = (e) => {
    const password = e.target.value;
    setPassword(password);
  };

  const getDeviceType = () => {
    let ua = navigator.userAgent;
    if (ua.match(/Android/i)) return "Android";
    else if (ua.match(/BlackBerry/i)) return "BlackBerry";
    else if (ua.match(/iPhone|iPad|iPod/i)) return "iOS";
    else if (ua.match(/Opera Mini/i)) return "Opera Mini";
    else if (ua.match(/IEMobile/i)) return "Windows Phone";
    else if (ua.match(/Linux/i)) return "Linux";
    else if (ua.match(/Windows/i)) return "Windows";
    else return "Apple";
  };

  const handleLogin = (e) => {
    e.preventDefault();

    if(!username.trim()) return toast.error("El nombre de usuario no puede estar vacío.")
    if(!password.trim()) return toast.error("La clave no puede estar vacía.")
    setLoading(true)
    AuthService.login({ username: username, password: password, device: getDeviceType() }).then(
      (result) => {
        if(result.status === 'success'){
          // Corregir esto, ya que se produce un error si utilizo navigate directamente.
          window.location.reload()
          //navigate("/");
        }else{
          if(result.data && result.data.error){
            toast.error(result.data.error)
          }
        }
        setLoading(false)
      },
      (e) => {
        toast.error(e) //e.response.data.error
      }
    );
  };

  return (
    
    <Container component="main" maxWidth="xs">

      <ToastContainer
        position="bottom-left"
        autoClose={3000}
        hideProgressBar
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />

      <Box component="form" onSubmit={handleLogin} noValidate sx={{ mt: 0 }}>
        <TextField
          type="text"
          required
          name="username"
          label="Usuario"
          fullWidth
          margin="normal"
          autoComplete="current-username"
          value={username}
          onChange={onChangeUsername}
        />
        <TextField
          type="password"
          required
          name="password"
          label="Clave"
          autoComplete="off"
          fullWidth
          margin="normal"
          value={password}
          onChange={onChangePassword}
        />

        <Button
          type="submit"
          disabled={ loading || !username || !password }
          fullWidth
          variant="contained"
          sx={{ mt: 3, mb: 2 }}
        >Acceder</Button>

        <Button
          type="submit"
          disabled={loading}
          fullWidth
          color="secondary"
          variant="contained"
          component={ Link } 
          to="/signup"
          sx={{ mb: 2 }}
        >Registrarse</Button>
      </Box>
    </Container>
  );
};

export default Login;
