import React, { useEffect, useCallback } from "react";
import { Link, useNavigate } from "react-router-dom";
import { styled } from '@mui/material/styles';
//Componentes.
import MuiAppBar from '@mui/material/AppBar';
import MuiDrawer from '@mui/material/Drawer';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import IconButton from '@mui/material/IconButton';
import Button from '@mui/material/Button';
// Iconos.
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import BarChartIcon from '@mui/icons-material/BarChart';
import SupervisedUserCircleIcon from '@mui/icons-material/SupervisedUserCircle';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import LoginIcon from '@mui/icons-material/Login';
import LogoutIcon from '@mui/icons-material/Logout';
import AccountCircleRoundedIcon from '@mui/icons-material/AccountCircleRounded';
// Servicios.
import AuthService from "services/auth.service";

const MenuAppBar = () => {
  let navigate = useNavigate();

  const drawerWidth = 200;
  const [open, setOpen] = React.useState(false);
  const [isAuthenticated, setIsAuthenticated] = React.useState(false);

  const [isUser, setIsUser] = React.useState(false);
  const [isMod, setIsMod] = React.useState(false);
  const [isAdmin, setIsAdmin] = React.useState(false);

  const getData = useCallback(async () => {
    const user = AuthService.getCurrentUser();

    if(user && user.roles){
      setIsAuthenticated(true);
      setIsUser((user.roles.includes('ROLE_USER')) ? true : false);
      setIsMod((user.roles.includes('ROLE_MODERATOR')) ? true : false);
      setIsAdmin((user.roles.includes('ROLE_ADMIN')) ? true : false);
    }

  }, []);

  const AppBar = styled(MuiAppBar, {
    shouldForwardProp: (prop) => prop !== 'open',
  })(({ theme, open }) => ({
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    ...(open && {
      marginLeft: drawerWidth,
      //width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    }),
  }));

  const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
      '& .MuiDrawer-paper': {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
          easing: theme.transitions.easing.sharp,
          duration: theme.transitions.duration.enteringScreen,
        }),
        boxSizing: 'border-box',
        ...(!open && {
          overflowX: 'hidden',
          transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
          }),
          width: theme.spacing(7),
          [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9),
          },
        }),
      },
    }),
  );

  const handleLogout = () => {
    AuthService.logout().then( () => {
        setIsAuthenticated(false)
        setIsUser(false)
        setIsMod(false)
        setIsAdmin(false)
        navigate("/login");
      }
    );
  }

  useEffect(() => {
    getData();
  },[getData]);

  return (

    <Box sx={{ display: 'flex' }}>
      <AppBar position="absolute" open={open}>
        <Toolbar sx={{ mr: 0, pr: 0 }} variant="dense">

          {isAuthenticated && (
            <IconButton
              edge="start"
              color="inherit"
              aria-label="open drawer"
              onClick={() => setOpen(!open)}
              sx={{ marginRight: '36px' }}
            >
              {(open) ?  <ChevronLeftIcon /> : <MenuIcon />}
            </IconButton>
          )}

          <Typography
            variant="h6"
            component=""
            color="inherit"
            noWrap
            sx={{ flexGrow: 1 }}
          > CRM-Baggio
          </Typography>

          {isAuthenticated ? (
            <div>
              <Button color="inherit" component={ Link } to="/login" onClick={ handleLogout } sx={{ mr: 0, pr: 0 }}>
                <LogoutIcon fontSize="small"/> 
              </Button>
            </div>
          ): (

            <Button color="inherit" component={ Link } to="/login">
              <LoginIcon fontSize="small" sx={{ p: 0, mr:'5px' }} /> Acceder
            </Button>
          )}

        </Toolbar>
      </AppBar>

      {isAuthenticated && (
        <Drawer variant="permanent" open={open}>
          <Toolbar
            sx={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-end',
              px: [1],
            }}
          >
          </Toolbar>
          <Divider />
            <List component="nav">
              <React.Fragment>

                  <ListItemButton component={ Link } to="/profile">
                    <ListItemIcon>
                      <AccountCircleRoundedIcon />
                    </ListItemIcon>
                    <ListItemText primary="Mi Cuenta" />
                  </ListItemButton>


                {isUser && (
                  <ListItemButton component={ Link } to="/user">
                    <ListItemIcon>
                      <BarChartIcon />
                    </ListItemIcon>
                    <ListItemText primary="Reportes" />
                  </ListItemButton>
                )}

                {isMod && (
                  <ListItemButton component={ Link } to="/mod">
                    <ListItemIcon>
                      <SupervisedUserCircleIcon />
                    </ListItemIcon>
                    <ListItemText primary="Moderador" />
                  </ListItemButton>
                )}

                {isAdmin && (
                  <ListItemButton component={ Link } to="/admin">
                    <ListItemIcon>
                      <AdminPanelSettingsIcon />
                    </ListItemIcon>
                    <ListItemText primary="Administrador" />
                  </ListItemButton>
                )}

              </React.Fragment>
            </List>
        </Drawer>
      )}

    </Box>

  );
};

export default MenuAppBar;
