import React, { useState, useEffect, useCallback } from "react";
import { useNavigate } from 'react-router-dom';
//import { parseISO, formatISO } from 'date-fns';

// Componentes Material.
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Alert from '@mui/material/Alert';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
// Toast.
import { ToastContainer, toast } from 'react-toastify';
// DataService. 
import UserService from "services/user.service"
import AuthService from "services/auth.service"

const Security = () => {

  let navigate = useNavigate();

  const [id, setId] = useState("");
  const [currentPassword, setCurrentPassword] = useState("");
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  const [logs, setLogs] = useState([]);

  const getData = useCallback( () => {
    UserService.getUpdatedUser().then((user) => {
      if(user){
        setId(user.id);
      }
    });
  }, []);

  const getLogs = useCallback( () => {
    UserService.getLogActivity().then((logs) => {
        if(logs && logs.data && logs.data.activity){
          setLogs(logs.data.activity)
          console.log(logs.data.activity)
        }
    });
  }, []);

  useEffect(() => {
    getData();
    getLogs();
  }, [getData, getLogs]);

  const onChangeCurrentPassword = (e) => { 
    const currentPassword = e.target.value 
    setCurrentPassword(currentPassword)
  }

  const onChangePassword = (e) => { 
      const password = e.target.value 
      setPassword(password)
  }

  const onChangePasswordConfirm = (e) => {
      const passwordConfirm = e.target.value
      setPasswordConfirm(passwordConfirm)
  }

  const handleUpdate = (event) => {
      event.preventDefault();

      if(!currentPassword) return toast.error("Debe ingresar su clave actual.")
      if(!password) return toast.error("Debe ingresar la clave nueva.")
      if(!passwordConfirm) return toast.error("Debe confirmar la clave nueva.")
      if(password !== passwordConfirm) return toast.error("Las claves ingresadas no coinciden.")

      const formData = {
          currentPassword: currentPassword,
          password: password,
          passwordConfirm: passwordConfirm,
      }

      setIsLoading(true);

      UserService.updatePassword(id, formData).then(
        (response) => {
          toast.success(response.data.message);
          setIsLoading(false);
          setTimeout(() => {
            AuthService.logout().then(() => { 
              navigate("/login"); 
            })
          } , 2000);
        },
        (error) => {
          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          toast.error(resMessage)
          setIsLoading(false);
        }
      );
  }

  return (
    <React.Fragment>
      <ToastContainer
        position="bottom-left"
        autoClose={3000}
        hideProgressBar
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
      
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Typography variant="h7" display="block" gutterBottom sx={{pt:3, pb: 0  }}>
              CONTRASEÑA
          </Typography>

          <Alert severity="info" sx={{mt:3, mb:3}}>
            La clave debe ingresada debe contener:  
            <li>Ocho Caracteres.</li>
            <li>Una Letra Mayúscula.</li>
            <li>Un Número.</li>
          </Alert>

          <form onSubmit={handleUpdate}>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={12} md={4}>
                  <TextField 
                      id="currentPassword"
                      name="currentPassword"
                      type="password"
                      fullWidth
                      label="Clave Actual" 
                      size="small"
                      value={currentPassword}
                      onChange={onChangeCurrentPassword}
                      variant="outlined" />
              </Grid>
              <Grid item xs={12} sm={6} md={4}>
                  <TextField 
                      id="password"
                      name="password"
                      type="password"
                      fullWidth
                      label="Nueva Clave" 
                      size="small"
                      value={password}
                      onChange={onChangePassword}
                      variant="outlined" />
              </Grid>
              <Grid item xs={12} sm={6} md={4}>
                  <TextField 
                      id="passwordConfirm"
                      name="passwordConfirm"
                      type="password"
                      fullWidth 
                      label="Repetir Clave" 
                      size="small"
                      value={passwordConfirm}
                      onChange={onChangePasswordConfirm}
                      variant="outlined" />
              </Grid>
              <Grid item xs={12} sx={{ mt:1, textAlign: {sm: 'left', xs: 'center'}}}>
                  <Button 
                    variant="contained"
                    size="small"
                    type="submit"
                    disabled={isLoading}
                  >Modificar Clave</Button>
              </Grid>
            </Grid>
          </form>
        </Grid>

        <Grid item xs={12}>
          <Typography variant="h7" display="block" gutterBottom sx={{pt:3, pb: 0  }}>
            ACTIVIDAD
          </Typography>

          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
              <TableHead>
                <TableRow>
                  <TableCell>Fecha y Hora</TableCell>
                  <TableCell>Dispositivo</TableCell>
                  <TableCell>IP</TableCell>
                  <TableCell>Acción</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {logs.map((row) => (
                  <TableRow
                    key={row.id}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                    <TableCell>{ row.createdAt }</TableCell>
                    <TableCell>{(row.device) ? row.device : '-' }</TableCell>
                    <TableCell>{(row.address) ? row.address  : '-' }</TableCell>
                    <TableCell>{(row.action) ? row.action : '-' }</TableCell>

                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>

        </Grid>
      </Grid>
    </React.Fragment>
  );
}

export default Security;